#header {
  display: inline-block;
  width: 100%;
  background-color: black;
  text-align: center;
  font-size: 16px; }

#header .container {
    display: inline-block;
    margin: 0 auto;
    width: 100%;
    padding: 0px; }

#header .container > * {
      float: left;
      display: inline-block;
      height: 70px;
      line-height: 70px; }

#header .container .logo {
      width: 150px;
      font-size: 1.5em;
      font-weight: 900;
      color: #fff;
      float: left; }

#header .container .user {
      width: 60px;
      font-size: 1em;
      color: #fff;
      font-weight: 900;
      float: right; }

#header .container .menu {
      width: calc(100% - 210px); }

#header .container .menu a {
        display: inline-block;
        padding: 0 1em;
        float: left;
        text-decoration: none;
        font-weight: 900;
        font-size: 1em;
        color: #fff;
        position: relative; }

#header .container .menu a:hover:after, #header .container .menu a:active:after {
          content: '';
          position: absolute;
          bottom: -3px;
          left: 0;
          border-bottom: 3px solid #fff;
          width: 100%; }

#header .container .menu a.active {
          border-bottom: 3px solid #fff; }

section .container {
  padding: 30px 15px; }

@media (min-width: 768px) {
    section .container {
      padding: 30px 10% 30px 10%; } }
